<template>
	<view>
		<view class="home_tab">
			<v-search></v-search>
			<view v-if="menuList.length>0">
				<u-tabs :list="menuList" :is-scroll="true" name="menuName" :current="swiperCurrent"
					active-color="rgb(212, 35, 122)" inactive-color="#606266" bg-color="rgb(238, 238, 239)"
					@change="tabsChange"></u-tabs>
				<!-- <u-tabs-swiper :list="menuList" bar-width="100" :bold="false" ref="accessTabs" active-color="rgb(212, 35, 122)" inactive-color="#606266" bg-color="rgb(238, 238, 239)" name="menuName" @change="tabsChange"
					:is-scroll="true">
				</u-tabs-swiper> -->
				<swiper height="calc(100vh - 380rpx)" :current="swiperCurrent" @transition="transition"
					@animationfinish="animationfinish">
					<swiper-item class="swiper-item" v-for="item,i in menuList" :key="i">
						<!-- <scroll-view class="article_scroll_view" scroll-y
							style="width: 100%;height:calc(100vh - 380rpx)">
							<component :is="'access-' + item.menuCode.toLowerCase()" :menuId="item.menuId"></component>
						</scroll-view> -->
					</swiper-item>
				</swiper>
			</view>
		</view>
	</view>
</template>

<script>
	import vSearch from "@/components/v-search";
	import accessDsjjcfw from "./access-dsjjcfw";
	import accessQllgj from "./access-qllgj";
	import accessSjzljcfw from "./access-sjzljcfw";
	import accessZnai from "./access-znai";
	import accessZtjcfw from "./access-ztjcfw";
	export default {
		components: {
			accessDsjjcfw,
			accessQllgj,
			accessSjzljcfw,
			accessZnai,
			accessZtjcfw,
			vSearch
		},
		data() {
			return {
				swiperCurrent: 0,
				menuList: [],
				current: 0
			};
		},
		onLoad() {
			this.getMenuList()
		},
		onShow() {

		},
		methods: {
			getMenuList() {
				this.request({
					url: "menu/list",
					data: {
						parentId: 1
					},
				}).then(data => {
					if (data && data.resphead && data.resphead.succeed) {
						this.menuList = data.respbody
						console.log('datassssss', data.respbody)

					} else {
						uni.showToast({
							title: data.resphead.message || "获取菜单失败",
							icon: "none"
						});
					}
				});
			},
			// tabs通知swiper切换
			tabsChange(index) {
				this.swiperCurrent = index;
			},
			// swiper-item左右移动，通知tabs的滑块跟随移动
			transition(e) {
				let dx = e.detail.dx;
				//this.$refs.accessTabs.setDx(dx);
			},
			// 由于swiper的内部机制问题，快速切换swiper不会触发dx的连续变化，需要在结束时重置状态
			// swiper滑动结束，分别设置tabs和swiper的状态
			animationfinish(e) {
				let current = e.detail.current;
				//this.$refs.accessTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
			// scroll-view到底部加载更多
			onreachBottom() {

			}
		}
	};
</script>

<style lang="scss">
	uni-swiper {
		height: calc(100vh - 380rpx)
	}

	.home_tab {
		.home_tab_title {
			position: relative;

			.title_inner {
				width: 70%;
				margin: 0 auto;
			}

			.iconsearch {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 5%;
			}
		}

		.home_tab_content {}
	}
</style>
